<script>
import moment from "moment";
import {detail, getBookOnSearch, getGenresBook} from "@/api/MovieRest";
import {collect} from "@/api/user";
export default {

  data() {
    return {
      list1:[],
      list2:[],
      dialog:[],
      dialogVisible: true,
      pageSize: 24,
      currentPage:1,
      count:0,
      genres: [],
      genresBook: '',
      InfoBook: [],
      RecsBook: [],
      RecsBCBook: [],
      collectvisual:false,
      searchV: true,
      form: this.$form.createForm(this),
      loading: false,
      loadingRate:false,
      outerVisible: false,
      text: '',
      select: '图书',
      checkTag:'小说',
      checkSearch:'',
      BookSearch: [],
      genresV: true,
    };
  },
  filters: {
    fromNow(date) {
      return moment(date).fromNow();
    },
  },
  mounted() {

    this.clickGenres('小说')
    this.collectVisual()
  },
  methods: {
    Close(){
      this.dialogVisible = true;
    },
    showDialog(book){
      window.scroll(0,0)
      this.dialogVisible = false;
      detail({bid: book}).then((res) => {
        console.log(res)
        this.dialog = res[0].book;
        this.list1 = res[1].recs1;
        this.list2 = res[2].recs2;
      })
    },
    onSearch(){
      this.checkSearch = this.text;
      this.searchV = true;
      this.genresV = false;
      this.pageSize = 24;
      this.currentPage = 1;
      this.count = 0;
      this.submitSear();
    },
    submitSear(){
      getBookOnSearch({text: this.checkSearch, currentPage: this.currentPage, pageSize: this.pageSize}).then((res)=>{
        this.count=res[0].count
        this.currentPage=res[1].currentPage
        this.BookSearch=res[2].books //这个地方需要注意   res.data.   res.
      });
    },

    clickGenres(info){
      this.checkTag = info;
      this.text = '';
      this.pageSize = 24;
      this.currentPage = 1;
      this.count = 0;
      this.genresV = true;
      this.searchV = false;
      this.submitGen();
    },
    submitGen(){
      getGenresBook({text: this.checkTag, currentPage: this.currentPage, pageSize: this.pageSize}).then((res)=>{
        this.count=res[0].count
        this.currentPage=res[1].currentPage
        this.genresBook=res[2].books //这个地方需要注意   res.data.   res.
      });
    },

    collectVisual(){
      if (window.sessionStorage.getItem('username')){
        this.collectvisual = true;
      }
    },
    Collection(book) {
      if(window.sessionStorage.getItem("username")){
        collect({username: window.sessionStorage.getItem('username'), bid: book}).then((res) => {
            this.$notify({
              title: '收藏成功',
              message: ("请到个人页面查看")
            });

        })
      }else{
        const h = this.$createElement;

        this.$notify({
          title: '请先登陆',
          message: h('i', { style: 'color: teal'}, '')
        });
      }
    },
    switchDialog(bid){
      window.sessionStorage.setItem("bid",bid)
      this.$router.replace({ name: 'BookDetail' })
    },
  },
};
</script>

